form {
    width: 360px;
}

.login-content{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
}

.login-content img{
    margin-top: 25px;
}

.login-content h2{
    margin: 15px 0;
    color: #333;
    text-transform: uppercase;
    font-size: 2.9rem;
}

.login-content .input-div{
    position: relative;
    display: grid;
    grid-template-columns: 7% 93%;
    margin: 25px 0;
    padding: 5px 0;
    border-bottom: 2px solid #d9d9d9;
}

.login-content .input-div.name{
    margin-top: 12px;
}

.i{
    color: #d9d9d9;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.i i{
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.input-div > div{
    position: relative;
    height: 45px;
}

.input-div > div > h5{
    position: absolute;
    left: 10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    color: #999;
    font-size: 15px;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

.input-div:before, .input-div:after{
    content: '';
    position: absolute;
    bottom: -2px;
    width: 0%;
    height: 2px;
    background-color: #38d39f;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
}

.input-div:before{
    right: 50%;
}

.input-div:after{
    left: 50%;
}

.input-div.focus:before, .input-div.focus:after{
    width: 50%;
}

.input-div.focus > div > h5{
    top: -5px;
    font-size: 15px;
}

.input-div.focus > .i > i{
    color: #38d39f;
}

.input-div > div > input{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    background: none;
    padding: 0.5rem 0.7rem;
    font-size: 1.2rem;
    color: #555;
    font-family: 'poppins', sans-serif;
}

/*.input-div.address{*/
    /*margin-bottom: 4px;*/
/*}*/

.btn{
    display: block;
    width: 100%;
    height: 50px;
    border-radius: 25px;
    outline: none;
    border: none;
    background-image: -webkit-gradient(linear, left top, right top, from(#534b40), color-stop(#534b40), to(#534b40));
    background-image: -webkit-linear-gradient(left, #534b40, #534b40, #534b40);
    background-image: -o-linear-gradient(left, #534b40, #534b40, #534b40);
    background-image: linear-gradient(to right, #534b40, #534b40, #534b40);
    background-size: 200%;
    font-size: 1.2rem;
    color: #fff;
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    margin: 1rem 0;
    cursor: pointer;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.btn:disabled{
    background: #dddddd;
    cursor: auto;
}

.btn:hover:enabled{
    background-image: -webkit-gradient(linear, left top, right top, from(black), to(black));
    background-image: -webkit-linear-gradient(left, black, black);
    background-image: -o-linear-gradient(left, black, black);
    background-image: linear-gradient(to right, black, black);
}

.text {
    color: #534b40;
    font-size: 14px;
    font-weight: bold;
}

.required {
    color: red;
}

@media screen and (max-width: 1000px){
    form {
        width: 290px;
    }

    .login-content h2{
        font-size: 2.4rem;
        margin: 8px 0;
    }

    .img img{
        width: 400px;
    }
}

@media screen and (max-width: 900px){
    .login-content{
        -ms-flex-pack: center;
            justify-content: center;
    }
}


